  font-weight: var(--feather-font-weight-medium);
  display: flex;
  margin-bottom: calc(-1 * var(--feather-space-4));
}
.PageHeader-headerContainer {
  flex: 1;
  min-width: 0;
}
.PageHeader-startContent,
.PageHeader-endContent {
  flex: 0 0 auto;
  vertical-align: middle;
  white-space: nowrap;
}
.PageHeader-startContent {
  display: flex;
}
.PageHeader-startContent > a,
.PageHeader-startContent > a:active,
.PageHeader-startContent > a:focus,
